    --size--border--radius: 4px;

    /* Animation */
    --animation-duration: 0.3s;
}

/* Universal tags */
* {
    box-sizing: border-box;
}

body{
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    cursor: default;
}

a {
    text-decoration: none;
    color: var(--color-white);
}

ul {
    list-style: none;
}

button {
    background-color: transparent;
    cursor: pointer;
    border: none;
    outline: none;
}

/* Typography */

h1 {
    font-size: var(--font-large);
    font-weight: var(--weight-bold);
    color: var(--color-black);
    margin: 16px 0px;
    padding: 5px;
}

h2 {
    font-size: var(--font-medium);
    font-weight: var(--weight-semi-bold);
    color: var(--color-black);
    margin: 8px 0;
}

h3 {
    font-size: var(--font-regular);
    font-weight: var(--weight-regular);
    color: var(--color-black);
    margin: 8px 0;
}

p {
    font-size: var(--font-regular);
    font-weight: var(--weight-regular);
    color: var(--color-black);
    margin: 4px 0;
}

/* Navber */

html{   /* 네브바 클릭시 부드럽게 이동 */
	scroll-behavior: smooth;
}

#navber {
    position:fixed;
    z-index: 5;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--color-white);
    padding: 5px;
    background-color: var(--color-purple);
}

.navber__menu {
    display: flex;
}

.navber__logo {
    font-size: var(--font-medium);
    font-weight: var(--weight-semi-bold);
}

.navber__logo img {
    width: 60%;
}

.navber__menu__item {
    padding: 8px 12px;
    margin: 0 4px;
    cursor: pointer;
    border-radius: var(--size--border--radius);
}

.navber__menu__item.active {
    border: 1px solid var(--color-white);
} 

.navber__menu__item:hover {
    background-color: var(--color-dark-purple);
}
/* Toggle button */
.navbar__toggle-btn {
    position: absolute;
    top: 15px;
    right: 32px;
    font-size: 24px;
    color: var(--color-white);
    display: none;
    /* 
    display대신 opacity: 0; 을 사용하면 
    화면상에 보이지는 않지만 마우스로 클릭됨
    */
}


/* mainPage */
#mainPage {
    background: url('@/assets/main_background1.png') center/cover no-repeat;
    padding-top: 69px;
    text-align: center;
}
.home__logo {
    width: 250px;
}
.home__title {
    margin-top: 5px;
}
.home__title,
.home__description {
    color: var(--color-white);
}

.home__contact {
    color: var(--color-white);
    font-size: var(--font-regular);
    font-weight: var(--weight-bold);
    margin: 24px;
    padding: 8px 12px;
    border: 2px solid var(--color-white);
    border-radius: 4px;
}

/* Section common */
.section {
    padding: 50px;
    text-align: center;
    max-width: 1200px;
    margin: auto;
}
