/* 38강 참조하기 */

/* hitproductPage 인기상품 */

/* deadlinePage 자정특가 */

/* recipePage 레시피 */
.recipePage__category {
    margin: 40px;
}

.category__btn {
    border: 1px solid var(--color-dark-white);
    border-radius: var(--size--border--radius);
    font-size: var(--font-regular);
    padding: 8px 30px;
}

.category__btn.active,
.category__btn:hover {
    background-color: var(--color-pink);
    color: var(--color-white);
}

.category__btn.active .category_count,
.category__btn:hover .category_count {
    opacity: 1;
    top: 0;
}

.category_count {
    background-color: var(--color-orange);
    border-radius: 50%;
    color: var(--color-white);
    width: 24px;
    height: 24px;
    line-height: 24px;
    display: inline-block;
    position: relative;
    top: -20px;
    left: 4px;
    opacity: 0;
    transition: all var(--animation-duration) ease;
}

.recipe_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

.list {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 360px;
    height: 350px;
    margin: 10px 2px;
    background-color: var(--color-light-white);
}

.list_img {
    max-width: 100%;
    max-height: 100%;
}

.list_description {
    position: absolute;
    background-color: var(--color-black);
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    opacity: 0;
    transform: translateY(10px);
    transition: all var(--animation-duration) ease-in;
}

.list_description h3 {
    color: var(--color-white);
}

.list:hover .list_description {
    opacity: 0.8;
    transform: translateY(0px);
}

.list_description h3::after {
    content: '';
    display: block;
    position: relative;
    left: 50%;
    width: 25px;
    height: 2px;
    margin-left: -12px;
    margin-top: 12px;
    background-color: var(--color-dark-white);
}

/* footer */
#footer {
    background-color: var(--color-purple);
}
#footer .section {
    padding: 10px;

}
.ft_section {  /* max-witdh 1200px이라 별도로 클래스 줬음 */
    padding: 50px;
    text-align: center;
    margin: auto;
}

.footer__title, .footer__number,
.footer__email, .footer__rights {
    color: var(--color-white);
}

.footer__title {
    margin: 10px 0;
}

.footer__links {
    font-size: var(--font-large);
    margin: 15px 0;
}

.footer__links i {
    transition: all var(--animation-duration) ease-in;
    padding: 0 10px;
}

.footer__links i:hover {
    transform: scale(1.1);
    color: var(--color-dark-white);
}

/* 반응형 For below 768px screen width */
@media screen and (max-width: 768px){
    /* 네브바 */
    .navbar__toggle-btn {
        display: block;
    }

    #navber {
        flex-direction: column;
        align-items: flex-start;
    }

    .navber__menu {
        flex-direction: column;
        text-align: center;
        width: 100%;
        display: none;

    }

    /* newproduct */
    .new-text-wrap h2{
    font-size: 15px;
    }

    .new-text-wrap p{
        display: none;
    }

    /* 레시피 */
    .list {
        flex-grow : 1;

    }
    

}
